.fuel-cost {
  h2 {
    margin-bottom: 48px;
  }
  table {
    width: 100%;
    border-collapse: collapse;

    thead {
      border: 1px solid #043c77;
      border-bottom: none;
    }

    th {
      text-align: left;
      padding: 19px 0 21px 30px;
      background: #043c77;

      font-weight: 600;
      font-size: 13px;
      line-height: 16px;
      color: #ffffff;

      &:first-child {
        padding-left: 20px;
      }
    }
    td {
      text-align: left;
      padding: 16px 0 16px 30px;

      font-weight: 600;
      font-size: 13px;
      line-height: 16px;

      border-bottom: 1px solid #e7e7e7;
      border-top: 1px solid #e7e7e7;

      &:last-child {
        border-right: 1px solid #e7e7e7;
      }
    }
  }
}

.td-title-style {
  padding-left: 20px;
  background: #098fcf;
  font-weight: 700 !important;
  font-size: 24px !important;
  line-height: 29px !important;
  color: #ffffff !important;
  border-left: 1px solid #098fcf !important;
  width: 14.55%;
  max-width: 163px;
}

.bl {
  border-left: 1px solid #e7e7e7;
}

.table-middle {
  margin-top: 20px;
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .fuel-cost table td {
    padding: 10px 5px;

    &.td-title-style {
      max-width: 107px;
      min-width: 90px;
    }
  }
}

@media (max-width: 576px) {
  .fuel-cost table th:first-child {
    padding-left: 5px;
  }
  .fuel-cost table th {
    padding: 15px 0 15px 11px;
  }
  table {
    display: block;
    overflow-x: auto;
  }
}

@media (max-width: 425px) {
  .fuel-cost h2 {
    margin-bottom: 20px;
  }

  .fuel-cost table td {
    text-align: center;
    font-size: 10px;
    line-height: 12px;

    &.td-title-style {
      padding-left: 0px;
      font-size: 16px !important;
      line-height: 18px !important;
      min-width: 60px;
    }
  }

  .fuel-cost table th {
    padding: 10px 0 10px 6px;
    font-size: 8px;
    line-height: 10px;
  }
}

@media (max-width: 375px) {
  .fuel-cost table th:first-child {
    padding-left: 0px;
  }

  .table-middle {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}
